import styled, { css } from 'styled-components'
import {
  Layout,
  Menu,
  Col,
  Drawer,
} from 'antd'
import {
  BellOutlined,
  BarsOutlined
} from '@ant-design/icons'

export const Wrapper = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  box-shadow: ${props => props.theme.shadows.blue};
  z-index: 20;
  border-bottom: 1px solid ${props => props.theme.colors.regularGrey};
`

export const Header = styled(Layout.Header)`
  padding: 0;
  background-color: ${props => props.theme.colors.white};
`

export const LeftCol = styled(Col)`
  width: calc(100% - 250px);
  padding-left: 250px;
  text-align: center;

  @media only screen and (max-width: ${props => props.theme.breakpoints.xl - 1}px) {
    padding-left: 65px;
  }

  @media only screen and (max-width: ${props => props.theme.breakpoints.md - 1}px) {
    padding-left: 95px;
    text-align: left;
  }

  @media only screen and (max-width: ${props => props.theme.breakpoints.sm - 1}px) {
    padding-left: 0;
  }
`

export const RightCol = styled(Col)`
  width: 250px;
`

export const Logo = styled.img`
  height: 30px;
  transform: translateY(-5px);

  @media only screen and (max-width: ${props => props.theme.breakpoints.md - 1}px) {
    height: 20px;
  }

  @media only screen and (max-width: ${props => props.theme.breakpoints.sm - 1}px) {
    display: none;
  }
`

export const Icon = css`
  font-size: 20px !important;
`

export const IconBellOutlined = styled(BellOutlined)`
  ${Icon}
`

export const IconBarsOutlined = styled(BarsOutlined)`
  ${Icon}
`

export const NavMenu = styled(Menu)`
  text-align: center;
  transform: translateY(-1px);
  border-color: ${props => props.theme.colors.regularGrey};
`

export const Item = css`
  color: ${props => props.theme.colors.strongGrey};

  &.ant-menu-item-active,
  &.ant-menu-item-selected {
    color: ${props => props.theme.colors.blue} !important;
    border-color: ${props => props.theme.colors.blue} !important;
  }

  &.ant-menu-item-selected {

    &:after {
      content: '';
      position: absolute;
      top: 100%;
      left: 50%;
      transform: translateX(-50%);
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 13px 7.5px 0 7.5px;
      border-color: ${props => props.theme.colors.blue} transparent transparent transparent;
    }
  }
`

export const NavMenuItem = styled(Menu.Item)`
  ${Item}
`

export const NavMenuAvatar = styled(Menu.Item)`
  ${Item}
`

export const DrawerPanel = styled(Drawer)`
  z-index: 10;
  padding-top: 60px;

  .ant-drawer-content-wrapper {
    box-shadow: none !important;
    border-left: 1px solid ${props => props.theme.colors.blue};
  }
`
