import styled, { keyframes } from 'styled-components'
import { Layout } from 'antd'

export const GlobalLayout = styled.div`
  width: 100%;
  height: 100%;
  padding: 100px 20px;

  @media only screen and (max-width: ${props => props.theme.breakpoints.md - 1}px) {
    padding: 20px;
  }
`

const open = keyframes`
  from {opacity: 0;}
  to {opacity: 1;}
`

export const PublicLayout = styled(GlobalLayout)`
  background: center no-repeat url(${process.env.PUBLIC_URL}/public-background.jpg);
  background-size: cover;
  animation: ${open} 500ms ease-in;
`

export const PrivateLayout = styled(Layout)`
  min-height: 100%;
  animation: ${open} 500ms ease-in;
`
