import { Auth } from 'aws-amplify'

interface ILogin {
  email: string,
  password: string
}

interface IRegister {
  firstName: string,
  lastName: string,
  email: string,
  password: string,
  addresses: any[]
}

interface IConfirmAccount {
  email: string,
  code: string
}

interface IRenewCode {
  email: string
}

interface IForgotPassword {
  email: string
}

export const loginRequest = async (params: ILogin) => {
  try {
    await Auth.signIn(params.email, params.password)
    const session = await Auth.currentSession()
    const accessToken = session.getIdToken().getJwtToken()
    return {status: 'success', data: {accessToken}}
  } catch (err) {
    return {status: 'error', data: err}
  }
}

export const registerRequest = async (params: IRegister) => {
  const options = {
    method: 'POST',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json'
    },
    body: JSON.stringify(params)
  }

  const response = await fetch(`${window.env.backends['ms-account']}/register`, options)
  if (!response.ok) return {
    status: 'error',
    data: response.statusText
  }
  const data = await response.json()
  return {
    status: 'success',
    data
  }
}

export const confirmAccountRequest = async (params: IConfirmAccount) => {
  const options = {
    method: 'POST',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json'
    },
    body: JSON.stringify(params)
  }

  const response = await fetch(`${window.env.backends['ms-account']}/confirm-code`, options)
  if (!response.ok) return {
    status: 'error',
    data: response.statusText
  }
  return {
    status: 'success',
    data: {}
  }
}

export const renewCodeRequest = async (params: IRenewCode) => {
  try {
    const response = await Auth.resendSignUp(params.email)
    return {status: 'success', data: response}
  } catch (err) {
    return {status: 'error', data: err}
  }
}

export const forgotPasswordRequest = async (params: IForgotPassword) => {
  try {
    const response = await Auth.forgotPassword(params.email)
    return {status: 'success', data: response}
  } catch (err) {
    return {status: 'error', data: err}
  }
}

export const sessionRequest = async () => {
  try {
    const session = await Auth.currentSession()
    const accessToken = session.getIdToken().getJwtToken()
    return {status: 'success', data: {accessToken}}
  } catch (err) {
    return {status: 'error', data: err}
  }
}
