import React, { useState } from 'react'
import { Input } from 'antd'
import { CheckOutlined, EyeOutlined, EyeInvisibleOutlined } from '@ant-design/icons'

interface IProps {
  type?: string,
  verified?: boolean,
  backgroundColor?: string,
  borderBottom?: string,
  handleChange: (value: string) => void
}

export default (props: IProps) => {
  const {
    type,
    verified,
    backgroundColor,
    borderBottom,
    handleChange
  } = props
  const [hiddenPassword, setHiddenPassword] = useState(true)

  return (
    <>
      <Input
        type={type && hiddenPassword ? type : 'text'}
        onChange={e => handleChange(e.target.value)}
        size='large'
        style={{
          backgroundColor: backgroundColor || '#eff1f6',
          border: 'none',
          borderBottom: borderBottom || 'none',
          borderRadius: '0'
        }}
        suffix={
          <>
            {type === 'password' && hiddenPassword ?
              <EyeOutlined
                style={{
                  cursor: 'pointer'
                }}
                onClick={() => setHiddenPassword(false)}
              />
            : null}
            {type === 'password' && !hiddenPassword ?
              <EyeInvisibleOutlined
                style={{
                  cursor: 'pointer'
                }}
                onClick={() => setHiddenPassword(true)}
              />
            : null}
            {verified ?
              <CheckOutlined
                style={{
                  marginLeft: '10px',
                  color: '#3bb037'
                }}
              />
            : null}
          </>
        }
      />
    </>
  )
}
