import { useState, useEffect } from 'react'

export default (queryParams: string) => {
  const [params, setParams] = useState<any>(null)

  useEffect(() => {
    if (queryParams !== '' && !params) {
      let newParams: any = {}

      queryParams.substr(1).split('&').forEach((param: any) => {
        const splitedParam = param.split('=')
        newParams[splitedParam[0]] = splitedParam[1]
      })

      setParams(newParams)
    }
  }, [queryParams,params])

  return params
}
