import React, { useState, useEffect, useCallback } from 'react'
import { withRouter, Redirect } from 'react-router-dom'
import { Layout } from 'antd'

import { useRecoilState } from 'recoil'
import { authState } from '../../store/auth'

import { configRequest } from '../../services/config'
import { sessionRequest } from '../../services/auth'

import Theme from './Theme'
import Transitions from './Transitions'
import Sider from './Sider'
import Header from './Header'
import Content from './Content'

import {
  PublicLayout,
  PrivateLayout
} from '../styled/layout/Layout'

interface IProps {
  history: any,
  location: any,
  match: any,
  children?: any
}

const GlobalLayout = (props: IProps) => {
  const { env } = window
  const { location, children } = props
  const { pathname } = location
  const [isLoadedConfig, setIsLoadedConfig] = useState(false)
  const [auth, setAuth] = useRecoilState(authState)
  const publicRoutes = [
    '/',
    '/login',
    '/register',
    '/forgot-password',
    '/welcome',
    '/confirm-account'
  ]

  const config = useCallback(() => {
    configRequest().then(response => {
      if (response.status === 'error') return
      setIsLoadedConfig(true)
    })
  }, [])

  const session = useCallback(() => {
    sessionRequest().then(response => {
      if (response.status === 'error') {
        setAuth({
          loaded: true,
          session: false,
          token: ''
        })
      }
      if (response.status === 'success') {
        setAuth({
          loaded: true,
          session: true,
          token: response.data.accessToken
        })
      }
    })
  }, [setAuth])

  useEffect(() => {
    if (!env) config()
  }, [env, config])

  useEffect(() => {
    if (isLoadedConfig) session()
  }, [isLoadedConfig, session])

  return (
    <>
      {auth.loaded && !auth.session && pathname === '/' ?
        <Redirect to='/login' />
      : null}
      {auth.loaded && !auth.session && !publicRoutes.includes(pathname) ?
        <Redirect to='/login' />
      : null}
      {auth.loaded && auth.session && publicRoutes.includes(pathname) ?
        <Redirect to='/home' />
      : null}
      {isLoadedConfig && auth.loaded ?
        <Theme>
          {auth.session ?
            <PrivateLayout>
              <Sider />
              <Layout>
                <Header />
                <Content>
                  <Transitions>
                    {children}
                  </Transitions>
                </Content>
              </Layout>
            </PrivateLayout>
          :
            <PublicLayout>
              <main>
                <Transitions>
                  {children}
                </Transitions>
              </main>
            </PublicLayout>
          }
        </Theme>
      : null}
    </>
  )
}

export default withRouter(GlobalLayout)
