import React, { useState } from 'react'
import { Link, withRouter } from 'react-router-dom'
import { message, Input } from 'antd'
import logo from '../../../../assets/img/logo.png'

import { useSetRecoilState } from 'recoil'
import { authState } from '../../../../store/auth'
// import { navigationState } from '../../../../store/navigation'

import { loginRequest } from '../../../../services/auth'
import { userRequest } from '../../../../services/user'

import VerifiedInput from '../../../modules/common/VerifiedInput'

import { InputWrapper, Label } from '../../../styled/common'
import {
  OneColForm,
  IdentitySection,
  Logo,
  Title,
  Information,
  ContextNav
} from '../../../styled/auth'

interface IProps {
  history: any,
  location: any,
  match: any,
}

interface IState {
  [key: string]: string
}

interface ILogin {
  email: string,
  password: string
}

interface IUser {
  token: string
}

const LoginForm = (props: IProps) => {
  const [state, setState] = useState<IState>({})
  const setAuth = useSetRecoilState(authState)
  // const navigate = useSetRecoilState(navigationState)

  const setInputValue = (key: string, value: string) => {
    let newSate = Object.assign({}, state)
    newSate[key] = value
    setState(newSate)
  }

  const login = (params: ILogin) => {
    message.destroy()
    message.loading('Connexion en cours', 0)
    loginRequest(params).then(response => {
      if (response.status === 'error') {
        message.destroy()
        message.error('Nous n\'avons pas pu vous identifier', 2.5)
        return
      }
      user({token: response.data.accessToken})
    })
  }

  const user = (params: IUser) => {
    userRequest(params).then(response => {
      message.destroy()
      if (response.status === 'error') {
        message.error('Nous n\'avons pas pu vous identifier', 2.5)
        return
      }
      setAuth({
        loaded: true,
        session: true,
        token: params.token
      })
    })
  }

  const submit = (e: any) => {
    e.preventDefault()
    login({
      email: state.email,
      password: state.password
    })
  }

  return (
    <OneColForm onSubmit={(e) => submit(e)}>
      <IdentitySection>
        <Logo src={logo} />
        <Title>Connexion</Title>
        <Information>Merci de renseigner vos identifiants</Information>
        <Label>Email</Label>
        <InputWrapper
          style={{marginBottom: '20px'}}
        >
          <VerifiedInput
            type='email'
            verified={true}
            handleChange={value => setInputValue('email', value)}
          />
        </InputWrapper>
        <Label>Password</Label>
        <InputWrapper>
          <VerifiedInput
            type='password'
            verified={true}
            handleChange={value => setInputValue('password', value)}
          />
        </InputWrapper>
        <Label style={{textAlign: 'right'}}>
          <Link to='/forgot-password'>Mot de passe oublié ?</Link>
        </Label>
        <Input
          type='submit'
          size='large'
          value='Je me connecte'
          disabled={false}
          style={{
            margin: '20px 0',
            border: 'none',
            borderRadius: '5px',
            backgroundColor: '#5889c4',
            color: '#fff',
            lineHeight: '40px',
            fontFamily: 'rubikmedium',
            cursor: 'pointer'
          }}
        />
        <ContextNav>
          <span>Vous souhaitez optimiser la gestion de votre copropriété ?</span>
          <Link to='/register'> Inscrivez-vous ici</Link>
          {/*<span onClick={() => navigate('/register')}>navigate</span>*/}
        </ContextNav>
      </IdentitySection>
    </OneColForm>
  )
}

export default withRouter(LoginForm)
