import React, { useState } from 'react'
import {
  Avatar,
  Space
} from 'antd'
import {
  UserOutlined
} from '@ant-design/icons'

import SenderEditor from './SenderEditor'
import SenderAttachments from './SenderAttachments'

import {
  Sender,
  Name,
  IconEditOutlined,
  AttachmentsCounter,
  Counter,
  IconPaperClipOutlined,
  IconSendOutlined,
  SenderTypeSelector,
  SenderTypeSelectorOption,
  SendButton
} from '../../../styled/dashboard/socialNetwork/Sender'

export default () => {
  const [type, setType] = useState('message')
  const [mode, setMode] = useState('editor')

  return (
    <Sender
      className={type}
      title={
        <Space>
          <Avatar
            icon={
              <UserOutlined />
            }
          />
          <Name>Anna Black</Name>
        </Space>
      }
      extra={
        <SenderTypeSelector
          defaultValue={type}
          bordered={false}
          dropdownClassName='sender-type-selector'
          dropdownMatchSelectWidth={false}
          onSelect={(value: any) => setType(value)}
        >
          <SenderTypeSelectorOption value='message'>Message</SenderTypeSelectorOption>
          <SenderTypeSelectorOption value='ads'>Annonce</SenderTypeSelectorOption>
          <SenderTypeSelectorOption value='options'>Sondage</SenderTypeSelectorOption>
          <SenderTypeSelectorOption value='question'>Question</SenderTypeSelectorOption>
          <SenderTypeSelectorOption value='alert'>Alerte</SenderTypeSelectorOption>
        </SenderTypeSelector>
      }
      actions={[
        <IconEditOutlined
          key='editor'
          className={mode === 'editor' ? 'selected' : ''}
          onClick={() => setMode('editor')}
        />,
        <AttachmentsCounter
          key='attachments'
          className={mode === 'attachments' ? 'selected' : ''}
          onClick={() => setMode('attachments')}
        >
          <Counter>4</Counter>
          <IconPaperClipOutlined
          />
        </AttachmentsCounter>,
        <SendButton
          type='primary'
          shape='circle'
          icon={
            <IconSendOutlined
              key='send'
            />
          }
        />
      ]}
    >
      {mode === 'editor' ?
        <SenderEditor />
      :
        null}
      {mode === 'attachments' ?
        <SenderAttachments />
      :
        null}
    </Sender>
  )
}
