import React, { useState } from 'react'

import {
  Wrapper,
  Sider,
  Header,
  IconMenuUnfoldOutlined,
  IconMenuFoldOutlined,
  Logo,
  IconAppstoreOutlined,
  IconTeamOutlined,
  IconSnippetsOutlined,
  IconEuroOutlined,
  IconDashboardOutlined,
  IconAuditOutlined,
  NavMenu,
  NavMenuItem
} from '../styled/layout/Sider'

export default () => {
  const [collapsedMenu, setCollapsedMenu] = useState(false)

  return (
    <Wrapper>
      <Sider
        width={250}
        trigger={null}
        collapsible
        collapsed={collapsedMenu}
      >
        <Header>
          {collapsedMenu ?
            <IconMenuUnfoldOutlined onClick={() => setCollapsedMenu(false)} />
            :
            <IconMenuFoldOutlined onClick={() => setCollapsedMenu(true)} />
          }
          <Logo
            className={collapsedMenu ? '' : 'open'}
            src={`${process.env.PUBLIC_URL}/logo.png`}
          />
        </Header>
        <NavMenu
          mode='inline'
        >
          <NavMenuItem
            key='1'
            icon={<IconAppstoreOutlined />}
          >Mon immeuble</NavMenuItem>
          <NavMenuItem
            key='2'
            icon={<IconTeamOutlined />}
          >Ma copropriété</NavMenuItem>
          <NavMenuItem
            key='3'
            icon={<IconSnippetsOutlined />}
          >Mes fournisseurs</NavMenuItem>
          <NavMenuItem
            key='4'
            icon={<IconEuroOutlined />}
          >Ma comptabilité</NavMenuItem>
          <NavMenuItem
            key='5'
            icon={<IconDashboardOutlined />}
          >Mes compteurs</NavMenuItem>
          <NavMenuItem
            key='6'
            icon={<IconAuditOutlined />}
          >Mes assemblées générales</NavMenuItem>
        </NavMenu>
      </Sider>
    </Wrapper>
  )
}
