import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { message, Input } from 'antd'
import logo from '../../../../assets/img/logo.png'

import { forgotPasswordRequest } from '../../../../services/auth'

import VerifiedInput from '../../../modules/common/VerifiedInput'

import { InputWrapper, Label } from '../../../styled/common'
import {
  OneColForm,
  IdentitySection,
  Logo,
  Title,
  Information,
  ContextNav
} from '../../../styled/auth'

interface IForgotPassword {
  email: string
}

export default () => {
  const [email, setEmail] = useState('')

  const forgotPassword = (params: IForgotPassword) => {
    message.destroy()
    message.loading('Réinitialisation de votre mot de passe en cours', 0)

    forgotPasswordRequest(params).then(response => {
      message.destroy()
      if (response.status === 'error') {
        message.error('Une erreur est surnvenue lors de la réinitialisation de votre mot de passe', 2.5)
        return
      }
      message.success('Vous avez récu un email de réinitialisation de votre mot de passe', 2.5)
    })
  }

  const submit = (e: any) => {
    e.preventDefault()
    forgotPassword({email})
  }

  return (
    <OneColForm onSubmit={(e) => submit(e)}>
      <IdentitySection>
        <Logo src={logo} />
        <Title>Mot de passe oublié ?</Title>
        <Information>Merci de renseigner votre email pour réinitialiser votre mot de passe</Information>
        <Label>Email</Label>
        <InputWrapper
          style={{marginBottom: '20px'}}
        >
          <VerifiedInput
            type='email'
            verified={true}
            handleChange={value => setEmail(value)}
          />
        </InputWrapper>
        <Input
          type='submit'
          size='large'
          value='Réinitialiser'
          disabled={false}
          style={{
            margin: '20px 0',
            border: 'none',
            borderRadius: '5px',
            backgroundColor: '#5889c4',
            color: '#fff',
            lineHeight: '40px',
            fontFamily: 'rubikmedium',
            cursor: 'pointer'
          }}
        />
        <ContextNav>
          <span>Vous souhaitez accéder à votre espace copropriété ?</span>
          <Link to='/login'> Connectez-vous ici</Link>
        </ContextNav>
      </IdentitySection>
    </OneColForm>
  )
}
