interface IMap {
  id: string
}

interface IAutocomplete {
  input: string
}

interface IPlaceDetail {
  selectedPlace: any,
  map: object
}

export const mapRequest = (params: IMap) => {
  const { google } = window
  const { id } = params

  return new google.maps.Map(
      document.getElementById(id), {
      center: new google.maps.LatLng(48.858307, 2.294631),
      minZoom: 10,
      maxZoom: 16,
      zoom: 15,
      zoomControl: false,
      mapTypeControl: false,
      scaleControl: false,
      streetViewControl: false,
      rotateControl: false,
      fullscreenControl: false,
      gestureHandling: 'cooperative'
    }
  )
}

let gmapAutocomplete: {service: any, sessionToken: any} = {
  service: null,
  sessionToken:  null
}
export const autocompleteRequest = (params: IAutocomplete) => {
  const { google } = window
  if (!gmapAutocomplete.service) gmapAutocomplete.service = new google.maps.places.AutocompleteService()
  if (!gmapAutocomplete.sessionToken) gmapAutocomplete.sessionToken = new google.maps.places.AutocompleteSessionToken()

  const request = {
    input: params.input,
    types: ['address'],
    componentRestrictions: { country: ['fr'] },
    sessionToken: gmapAutocomplete.sessionToken
  }

  return new Promise(function(resolve, reject) {
    gmapAutocomplete.service.getPlacePredictions( request, (response: any, status: any) => {
      if (status !== google.maps.places.PlacesServiceStatus.OK) {
        reject({
          options: [],
          sessionToken: ''
        })
      } else {
        resolve({
          options: response || [],
          sessionToken: gmapAutocomplete.sessionToken
        })
      }
    })
  })
}

let gmapPlaces: {service: any} = {
  service: null
}
export const placesRequest = (params: IPlaceDetail) => {
  const { google } = window
  const request = {
    placeId: params.selectedPlace.place_id,
    fields: [
      'address_component',
      'adr_address',
      'formatted_address',
      'geometry',
      'name',
      'place_id',
      'type'
    ],
    sessionToken: params.selectedPlace.sessionToken
  }
  if (!gmapPlaces.service) gmapPlaces.service = new google.maps.places.PlacesService(params.map)

  return new Promise(function(resolve, reject) {
    gmapPlaces.service.getDetails( request, (response: any, status: any) => {
      if (status !== google.maps.places.PlacesServiceStatus.OK) {
        reject({})
      } else {
        resolve({
          label: response.formatted_address,
          placeId: response.place_id,
          streetNumber: response.address_components.find((item: any) =>
            item.types.includes('street_number')
          ).long_name,
          streetName: response.address_components.find((item: any) =>
            item.types.includes('route')
          ).long_name,
          city: response.address_components.find((item: any) =>
            item.types.includes('locality')
          ).long_name,
          postalCode: response.address_components.find((item: any) =>
            item.types.includes('postal_code')
          ).long_name,
          department: response.address_components.find((item: any) =>
            item.types.includes('administrative_area_level_2')
          ).long_name,
          region: response.address_components.find((item: any) =>
            item.types.includes('administrative_area_level_1')
          ).long_name,
          country: response.address_components.find((item: any) =>
            item.types.includes('country')
          ).long_name,
          position: {
            lat: response.geometry.location.lat(),
            lng: response.geometry.location.lng()
          }
        })
      }
    })
  })
}
