import React, { useEffect, useCallback } from 'react'
import { withRouter } from 'react-router-dom'
import {
  SwitchTransition,
  CSSTransition
} from 'react-transition-group'

import { useRecoilState } from 'recoil'
import { navigationState } from '../../store/navigation'

import { Fade } from '../styled/layout/Transitions'

interface IProps {
  history: any,
  location: any,
  match: any,
  children: any
}

const Transition = (props: IProps) => {
  const { children, history, location } = props
  const { pathname } = location
  const [pathToNavigate, setPathToNavigate] = useRecoilState(navigationState)

  const navigate = useCallback(() => {
    setTimeout(() => {
      history.push(pathToNavigate)
    }, 100)
  }, [history, pathToNavigate])

  useEffect(() => {
    if (pathToNavigate.length && pathToNavigate !== pathname) navigate()
    if (pathToNavigate.length && pathToNavigate === pathname) setPathToNavigate('')
  }, [pathToNavigate, pathname, navigate, setPathToNavigate])

  return (
    <SwitchTransition>
      <CSSTransition
        key={location.key}
        timeout={150}
        classNames={'fade'}
      >
        <Fade
          className={
            pathToNavigate.length && pathToNavigate !== pathname ?
            'closing'
            :
            ''
          }
        >
          {children}
        </Fade>
      </CSSTransition>
    </SwitchTransition>
  )
}

export default withRouter(Transition)
