import React, { useState, useEffect } from 'react'
import {
  Row,
  Avatar
} from 'antd'
import { UserOutlined } from '@ant-design/icons'

import {
  Wrapper,
  Header,
  LeftCol,
  RightCol,
  Logo,
  IconBellOutlined,
  IconBarsOutlined,
  NavMenu,
  NavMenuItem,
  NavMenuAvatar,
  DrawerPanel
} from '../styled/layout/Header'

export default () => {
  const [panel, setPanel] = useState(true)
  const [selectedKey, setSelectedKey] = useState(['2'])

  useEffect(() => {
    if (selectedKey.length) {
      setPanel(true)
    }
  }, [selectedKey])

  useEffect(() => {
    if (!panel) {
      setSelectedKey([])
    }
  }, [panel])

  return (
    <>
      <Wrapper>
        <Header>
          <Row>
            <LeftCol>
              <Logo src={`${process.env.PUBLIC_URL}/logo.png`} />
            </LeftCol>
            <RightCol>
              <NavMenu
                mode='horizontal'
                selectedKeys={selectedKey}
              >
                <NavMenuItem
                  key='1'
                  icon={
                    <IconBellOutlined />
                  }
                  onClick={() => setSelectedKey(['1'])}
                />
                <NavMenuItem
                  key='2'
                  icon={
                    <IconBarsOutlined />
                  }
                  onClick={() => setSelectedKey(['2'])}
                />
                <NavMenuAvatar
                  key='3'
                  icon={
                    <Avatar
                      icon={
                        <UserOutlined />
                      }
                    />
                  }
                  onClick={() => setSelectedKey(['3'])}
                />
              </NavMenu>
            </RightCol>
          </Row>
        </Header>
      </Wrapper>
      <DrawerPanel
        placement='right'
        closable={true}
        width={250}
        visible={panel}
        mask={false}
        getContainer={false}
        onClose={() => setPanel(false)}
      >
      </DrawerPanel>
    </>
  )
}
