import React, { Component } from 'react'

import {
  TextWrapper,
  Strong,
  Regular,
  IconWrapper
} from '../../../styled/common/StyledPlaces'

interface IProps {
  firstLine?: string,
  secondLine?: string,
  icon?: any,
  backgroundColor?: string,
  color?: string,
  borderTop?: string,
  handleClick?: any,
  handleIconClick?: any
}

interface IState {}

class AddressItem extends Component<IProps, IState> {
  render() {
    const {
      firstLine,
      secondLine,
      icon,
      backgroundColor,
      color,
      borderTop,
      handleClick,
      handleIconClick
    } = this.props

    return (
      <div
        onClick={(e) => handleClick ? handleClick(e) : {}}
        style={{
          backgroundColor: backgroundColor ? backgroundColor : '#fff',
          color: color ? color : '#2e384d',
          borderTop: borderTop ? borderTop : 'none',
          cursor: handleClick ? 'pointer' : 'auto'
        }}
      >
        <TextWrapper>
          <Strong>{firstLine}</Strong>
          <Regular>{secondLine}</Regular>
        </TextWrapper>
        <IconWrapper
          onClick={(e) => handleIconClick ? handleIconClick(e) : {}}
        >{icon}</IconWrapper>
      </div>
    )
  }
}

export default AddressItem
