import React, { useState } from 'react'
import {
  Tooltip
} from 'antd'

import PostCommentAttachments from './PostCommentAttachments'

import {
  PostComment,
  PostCommentAvatar,
  PostCommentLikeModule,
  IconLikeOutlined,
  IconLikeFilled,
  PostCommentContent,
  PostCommentName,
  PostCommentDate,
  PostCommentActions,
  PostCommentActionsMenu,
  PostCommentActionsMenuItem,
  PostCommentActionsIcon,
  PostCommentTextCol
} from '../../../styled/dashboard/socialNetwork/PostComment'

interface IProps {
  className?: string
}

export default (props: IProps) => {
  const { className } = props
  const [liked, setLiked] = useState(false)
  const [likeCounter, setLikeCounter] = useState(2)

  return (
    <PostComment
      className={className}
      avatar={
        <PostCommentAvatar
          src='https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png'
          alt='Han Solo'
        />
      }
      author={
        <>
          <span className='date-name'>
            <PostCommentName>Han Solo</PostCommentName>
            <PostCommentDate>10 mai 2020 à 18h12</PostCommentDate>
          </span>
          <PostCommentLikeModule key='comment-basic-like'>
            <Tooltip title={liked ? 'Je n\'aime plus' : 'J\'aime'}>
              {liked ?
                <IconLikeFilled
                  onClick={() => {
                    setLiked(false)
                    setLikeCounter(likeCounter - 1)
                  }}
                />
              :
                <IconLikeOutlined
                  onClick={() => {
                    setLiked(true)
                    setLikeCounter(likeCounter + 1)
                  }}
                />
              }
            </Tooltip>
            <span>{likeCounter}</span>
          </PostCommentLikeModule>
        </>
      }
      content={
        <PostCommentContent>
          <PostCommentTextCol>
            We supply a series of design principles, practical patterns and high quality design
            resources (Sketch and Axure), to help people create their product prototypes beautifully
            and efficiently.
          </PostCommentTextCol>
          <PostCommentAttachments />
        </PostCommentContent>
      }
    >
      <PostCommentActions
        placement={className === 'reverse' ? 'bottomLeft' : 'bottomRight'}
        overlay={
          <PostCommentActionsMenu>
            <PostCommentActionsMenuItem>Action 1</PostCommentActionsMenuItem>
            <PostCommentActionsMenuItem>Action 2</PostCommentActionsMenuItem>
            <PostCommentActionsMenuItem>Action 3</PostCommentActionsMenuItem>
          </PostCommentActionsMenu>
        }
        trigger={['click']}
      >
        <PostCommentActionsIcon />
      </PostCommentActions>
    </PostComment>
  )
}
