import styled, { css } from 'styled-components'
import {
  Card,
  Select,
  Button
} from 'antd'
import {
  EditOutlined,
  PaperClipOutlined,
  SendOutlined
} from '@ant-design/icons'

export const Sender = styled(Card)`
  margin-bottom: 30px;
  box-shadow: ${props => props.theme.shadows.blue};
  border-radius: 5px;
  border-color: ${props => props.theme.colors.regularGrey};

  &.options {

    .text-editor {
      width: 50%;

      @media only screen and (max-width: ${props => props.theme.breakpoints.sm - 1}px) {
        width: 100%;
      }
    }

    .options-editor {
      display: block;
      width: 50%;

      @media only screen and (max-width: ${props => props.theme.breakpoints.sm - 1}px) {
        width: 100%;
      }
    }
  }

  &:not(.options) {

    .text-editor {
      width: 100%;
    }

    .options-editor {
      display: none;
    }
  }

  .ant-card-head {
    padding: 0 10px;
    border-bottom: none;

    .ant-card-head-title,
    .ant-card-extra {
      padding: 10px 0;
    }
  }

  .ant-card-body {
    padding: 0;
    border-top: 1px solid ${props => props.theme.colors.regularGrey};
  }

  .ant-card-actions {
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    border-color: ${props => props.theme.colors.regularGrey};
  }
`

export const Name = styled.div`
  font-size: 14px;
  font-family: ${props => props.theme.fonts.medium};
  color: ${props => props.theme.colors.darkBlue};
`

export const Selected = css`
  color: ${props => props.theme.colors.blue} !important;

  .anticon {
    color: ${props => props.theme.colors.blue} !important;
  }

  &:after {
    content: '';
    display: block;
    margin: 0 auto;
    width: 5px;
    height: 5px;
    background-color: ${props => props.theme.colors.blue} !important;
    border-radius: 50%;
  }
`

export const Icon = css`
  font-size: 20px !important;
  color: ${props => props.theme.colors.strongGrey} !important;

  &.selected {
    ${Selected}
  }
`

export const IconEditOutlined = styled(EditOutlined)`
  ${Icon}
`

export const AttachmentsCounter = styled.span`
  &.selected {
    ${Selected}

    > * {
      color: ${props => props.theme.colors.blue};
    }
  }

  > * {
    display: inline-block;
    vertical-align: top;
    color: ${props => props.theme.colors.strongGrey};
  }
`

export const Counter = styled.span`
  height: 20px;
  padding-top: 3px;
  font-family: ${props => props.theme.fonts.medium};
  font-size: 12px;
`

export const IconPaperClipOutlined = styled(PaperClipOutlined)`
  ${Icon}
`

export const IconSendOutlined = styled(SendOutlined)`
  font-size: 16px;
  transform: translateX(2px);
`

export const SendButton = styled(Button)`
  background-color: ${props => props.theme.colors.blue};
  border-color: ${props => props.theme.colors.blue};
  font-size: 12px;

  &:hover,
  &:focus {
    background-color: ${props => props.theme.colors.blue};
    border-color: ${props => props.theme.colors.blue};
  }
`

export const SenderTypeSelector = styled(Select)`
  font-size: 12px;
  font-family: ${props => props.theme.fonts.medium};
  color: ${props => props.theme.colors.blue};

  .ant-select-arrow {
    top: 50%;
    color: ${props => props.theme.colors.blue};
  }
`

export const SenderTypeSelectorOption = styled(Select.Option)`
  background-color: transparent;
`
