import styled from 'styled-components'
import {
  Card,
  Menu,
  Dropdown,
  Button,
  Col
} from 'antd'
import {
  EllipsisOutlined,
  CommentOutlined
} from '@ant-design/icons'

export const Post = styled(Card)`
  margin-bottom: 30px;
  box-shadow: ${props => props.theme.shadows.blue};
  border-radius: 5px;
  border-color: ${props => props.theme.colors.regularGrey};

  &.options {

    .text-editor {
      width: 50%;

      @media only screen and (max-width: ${props => props.theme.breakpoints.sm - 1}px) {
        width: 100%;
      }
    }

    .options-editor {
      display: block;
      width: 50%;

      @media only screen and (max-width: ${props => props.theme.breakpoints.sm - 1}px) {
        width: 100%;
      }
    }
  }

  &:not(.options) {

    .text-editor {
      width: 100%;
    }

    .options-editor {
      display: none;
    }
  }

  .ant-card-head {
    padding: 0 10px;
    border-bottom: none;

    .ant-card-head-title,
    .ant-card-extra {
      padding: 10px 0;
    }
  }

  .ant-card-body {
    padding: 0;

    .ant-row {
      align-items: center;
    }
  }

  .ant-card-actions {
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    border-color: ${props => props.theme.colors.regularGrey};
  }
`

export const Name = styled.div`
  font-size: 14px;
  font-family: ${props => props.theme.fonts.medium};
  color: ${props => props.theme.colors.darkBlue};
`

export const Timestamp = styled.div`
  font-size: 11px;
  font-family: ${props => props.theme.fonts.regular};
  color: ${props => props.theme.colors.strongGrey};
`

export const PostType = styled.span`
  font-size: 12px;
  font-family: ${props => props.theme.fonts.regular};
  color: ${props => props.theme.colors.blue};

  > * {
    display: inline-block;
    vertical-align: middle;
  }
`

export const PostStatus = styled.strong`
  font-family: ${props => props.theme.fonts.medium};
`

export const PostStatusColor = styled.span`
  width: 8px;
  height: 8px;
  margin-left: 8px;
  border-radius: 50%;
  background-color: ${props => props.theme.colors.green};
`

export const PostActions = styled(Dropdown)`
  margin-left: 15px;
`

export const PostActionsIcon = styled(EllipsisOutlined)`
  font-size: 30px;
  line-height: 10px;
  color: ${props => props.theme.colors.strongGrey};
`

export const PostActionsMenu = styled(Menu)`
  border-radius: 5px;
`

export const PostActionsMenuItem = styled(Menu.Item)`
  font-size: 12px;
  color: ${props => props.theme.colors.darkBlue};
  font-family: ${props => props.theme.fonts.regular};
`

export const ReadCommentsButton = styled(Button)`
  background-color: ${props => props.theme.colors.lightGrey};
  border-color: ${props => props.theme.colors.regularGrey};
  color: ${props => props.theme.colors.blue};
  font-family: ${props => props.theme.fonts.medium};
  font-size: 12px;

  &:hover,
  &:focus {
    background-color: ${props => props.theme.colors.lightGrey};
    border-color: ${props => props.theme.colors.regularGrey};
    color: ${props => props.theme.colors.blue};
  }
`

export const IconCommentOutlined = styled(CommentOutlined)`
  font-size: 16px;
  padding-left: 20px;
`

export const TextCol = styled(Col)`
  display: flex;
  align-items: center;
  width: calc(100% - 230px);
  padding: 10px 30px;
  color: ${props => props.theme.colors.darkBlue};

  @media only screen and (max-width: ${props => props.theme.breakpoints.sm - 1}px) {
    width: 100%;
    padding: 10px;
  }
`
