import React, { useState } from 'react'
import { Link, withRouter } from 'react-router-dom'
import { message, Row, Col, Input } from 'antd'
import logo from '../../../../assets/img/logo.png'

import { registerRequest } from '../../../../services/auth'

import VerifiedInput from '../../common/VerifiedInput'
import PlacesSelector from '../../common/places/Selector'

import { InputWrapper, Label } from '../../../styled/common'
import {
  TwoColsForm,
  IdentitySection,
  MapSection,
  Map,
  Logo,
  Title,
  Information,
  ContextNav,
  RegisterSubmitSection
} from '../../../styled/auth'

interface IProps {
  history: any,
  location: any,
  match: any,
}

interface IState {
  [key: string]: string
}

interface IAdresses {
  [key: number]: any[]
}

interface IRegister {
  firstName: string,
  lastName: string,
  email: string,
  password: string,
  addresses: any
}

const RegisterForm = (props: IProps) => {
  const { history } = props
  const [state, setState] = useState<IState>({})
  const [addresses, setAddresses] = useState<IAdresses>([])

  const setInputValue = (key: string, value: string) => {
    let newState = Object.assign({}, state)
    newState[key] = value
    setState(newState)
  }

  const register = (params: IRegister) => {
    message.destroy()
    message.loading('Inscription en cours', 0)
    registerRequest(params).then(response => {
      message.destroy()
      if (response.status === 'error') {
        message.error('Une erreur est surnvenue lors de votre inscription', 2.5)
        return
      }
      localStorage.setItem('password', state.password)
      history.push('/welcome')
    })
  }

  const submit = (e: any) => {
    e.preventDefault()
    register({
      firstName: state.firstName,
      lastName: state.lastName,
      email: state.email,
      password: state.password,
      addresses: addresses
    })
  }

  return (
    <TwoColsForm onSubmit={(e) => submit(e)}>
      <Row>
        <Col
          xs={{span: 24}}
          sm={{span: 24}}
          md={{span: 12}}
        >
          <IdentitySection>
            <Logo src={logo} />
            <Title>Créer mon immeuble</Title>
            <Information>Merci de renseigner vos identifiants et de localiser votre immeuble</Information>
            <Label>Prénom</Label>
            <InputWrapper
              style={{marginBottom: '5px'}}
            >
              <VerifiedInput
                type='text'
                verified={true}
                handleChange={value => setInputValue('firstName', value)}
              />
            </InputWrapper>
            <Label style={{textAlign: 'left'}}>Nom</Label>
            <InputWrapper
              style={{marginBottom: '20px'}}
            >
              <VerifiedInput
                type='text'
                verified={true}
                handleChange={value => setInputValue('lastName', value)}
              />
            </InputWrapper>
            <Label>Email</Label>
            <InputWrapper
              style={{marginBottom: '20px'}}
            >
              <VerifiedInput
                type='email'
                verified={true}
                handleChange={value => setInputValue('email', value)}
              />
            </InputWrapper>
            <Row>
              <Col
                xs={{span: 12}}
                sm={{span: 12}}
                md={{span: 12}}
                style={{
                  paddingRight: '5px'
                }}
              >
                <Label>Password</Label>
                <InputWrapper>
                  <VerifiedInput
                    type='password'
                    verified={true}
                    handleChange={value => setInputValue('password', value)}
                  />
                </InputWrapper>
              </Col>
              <Col
                xs={{span: 12}}
                sm={{span: 12}}
                md={{span: 12}}
                style={{
                  paddingLeft: '5px'
                }}
              >
                <Label style={{textAlign: 'left'}}>Confirmation</Label>
                <InputWrapper
                  style={{marginBottom: '20px'}}
                >
                  <VerifiedInput
                    type='password'
                    verified={true}
                    handleChange={value => console.log(value)}
                  />
                </InputWrapper>
              </Col>
            </Row>
            <ContextNav>
              <span>Vous possédez déjà un compte ?</span>
              <Link to='/login'> Connectez-vous ici</Link>
            </ContextNav>
          </IdentitySection>
        </Col>
        <Col
          xs={{span: 24}}
          sm={{span: 24}}
          md={{span:12}}
        >
          <MapSection>
            <Map id='places-map' />
            <RegisterSubmitSection>
              <PlacesSelector handlePlaces={(places) => setAddresses(places)} />
              <Input
                type='submit'
                size='large'
                value='Je crée mon immeuble'
                disabled={false}
                style={{
                  marginBottom: '20px',
                  border: 'none',
                  borderRadius: '5px',
                  backgroundColor: '#5889c4',
                  color: '#fff',
                  lineHeight: '40px',
                  fontFamily: 'rubikmedium',
                  cursor: 'pointer'
                }}
              />
            </RegisterSubmitSection>
          </MapSection>
        </Col>
      </Row>
    </TwoColsForm>
  )
}

export default withRouter(RegisterForm)
