interface IUser {
  token: string
}

export const userRequest = async (params: IUser) => {
  const options = {
    method: 'GET',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      authorization: `Bearer ${params.token}`
    }
  }

  const response = await fetch(`${window.env.backends['ms-account']}/me`, options)
  if (!response.ok) return {
    status: 'error',
    data: response.statusText
  }
  const data = await response.json()
  return {
    status: 'success',
    data
  }
}
