import React, { useState, useEffect } from 'react'
import { PlusOutlined, CloseOutlined } from '@ant-design/icons'

import { mapRequest } from '../../../../services/google'

import PlacesItem from './Item'
import PlacesAutocomplete from './Autocomplete'

import useScript from '../useScript'

import {
  SearchPlacesWrapper,
  PlacesItemsWrapper,
  PlacesCounter
} from '../../../styled/common/StyledPlaces'

interface IProps {
  handlePlaces: (places: any[]) => void
}

export default (props: IProps) => {
  const { env, google } = window
  const { handlePlaces } = props
  const [searchMode, setSearchMode] = useState(true)
  const [places, setPlaces] = useState<any[]>([])
  const script = useScript(`https://maps.googleapis.com/maps/api/js?key=${env.googleApiKey}&libraries=places`)
  const [map, setMap] = useState({})

  useEffect(() => {
    if (google) setMap(mapRequest({id: 'places-map'}))
  }, [google])

  useEffect(() => {
    if (places.length === 0) {
      setSearchMode(true)
    }
  }, [places])

  const setNewPlace = (newPlace: any) => {
    const isDuplicatedPlace = places.find(place => place.placeId === newPlace.placeId) !== undefined
    if (Object.keys(newPlace).length !== 0 && !isDuplicatedPlace) {
      const newPlaces = [...places, newPlace]
      setPlaces(newPlaces)
      handlePlaces(newPlaces)
      setSearchMode(false)
    }
  }

  const deletePlace = (placeId: string) => {
    const newPlaces = places.filter(place => place.placeId !== placeId)
    setPlaces(newPlaces)
    handlePlaces(newPlaces)
  }

  return (
    <>
      {script.loaded && !script.error ?
        <>
          {places.length ?
            <>
              <PlacesItemsWrapper>
                {places.map((place, i) =>
                  <PlacesItem
                    key={place.placeId}
                    firstLine={`${place.streetNumber} ${place.streetName}`}
                    secondLine={`${place.postalCode} ${place.city.toUpperCase()} - ${place.country.toUpperCase()}`}
                    icon={<CloseOutlined />}
                    color='#5889c4'
                    borderTop={i === 0 ? 'none' : '1px dashed #5889c4'}
                    handleIconClick={() => deletePlace(place.placeId)}
                  />
                )}
              </PlacesItemsWrapper>
            </>
          : null}
          {!searchMode ?
            <SearchPlacesWrapper>
              <PlacesItem
                firstLine='Ajouter une adresse'
                secondLine='(Copropriété comportant plusieurs entrées)'
                icon={<PlusOutlined />}
                color='#fff'
                backgroundColor='#5889c4'
                handleClick={() => setSearchMode(true)}
              />
            </SearchPlacesWrapper>
          :null}
          {searchMode ?
            <PlacesAutocomplete
              map={map}
              handleSelectPlace={place => setNewPlace(place)}
              style={{
                width: '100%',
                marginBottom: places.length ? '0' : '40px'
              }}
              autoFocus={places.length ? true : false}
              placeholder='Rechercher mon adresse'
            />
          :null}
          {places.length ?
            <PlacesCounter>{`${places.length} adresses sélectionnées`}</PlacesCounter>
          : null}
        </>
      : null}
    </>
  )
}
