import React, { useState, useEffect, useCallback } from 'react'
import { withRouter } from 'react-router-dom'

import { useSetRecoilState } from 'recoil'
import { authState } from '../../../../store/auth'

import { loginRequest, confirmAccountRequest } from '../../../../services/auth'
import { userRequest } from '../../../../services/user'

import useQueryParams from '../../common/useQueryParams'

interface IProps {
  history: any,
  location: any,
  match: any,
}

interface ILogin {
  email: string,
  password: string
}

interface IUser {
  token: string
}

interface IConfirmAccount {
  email: string,
  code: string
}

const ConfirmAccountForm = (props: IProps) => {
  const { history, location } = props
  const queryParams = useQueryParams(location.search)
  const password: string = localStorage.getItem('password') || ''
  const [perform, setPerform] = useState(false)
  const setAuth = useSetRecoilState(authState)

  const user = useCallback((params: IUser) => {
    userRequest(params).then(response => {
      if (response.status === 'error') return history.push('/login')
      setAuth({
        loaded: true,
        session: true,
        token: params.token
      })
    })
  }, [history, setAuth])

  const login = useCallback((params: ILogin) => {
    loginRequest(params).then(response => {
      if (response.status === 'error') return history.push('/login')
      user({token: response.data.accessToken})
    })
  }, [history, user])

  const confirmAccount = useCallback((params: IConfirmAccount) => {
    confirmAccountRequest(params).then(response => {
      if (response.status === 'error') return history.push('/login')
      login({
        email: queryParams.email,
        password
      })
    })
  }, [history, login, queryParams, password])

  useEffect(() => {

    if (queryParams && (!queryParams.email || !queryParams.code)) history.push('/login')
    if (!perform && queryParams && queryParams.email && queryParams.code) {
      confirmAccount({
        email: queryParams.email,
        code: queryParams.code
      })
      setPerform(true)
    }

  }, [queryParams, password, history, confirmAccount, perform])

  return (
    <>
      <div>Confirm Account</div>
      {/*confirmAccount.status === 'error' ?
        <button onClick={() => setPerformRenewCode(true)}>Renew code</button>
      : null*/}
    </>
  )
}

export default withRouter(ConfirmAccountForm)
