import React, { useState } from 'react'
import { AutoComplete, Input } from 'antd'
import { SearchOutlined } from '@ant-design/icons'
import throttle from 'lodash/throttle'

import { autocompleteRequest, placesRequest } from '../../../../services/google'

interface IProps {
  map: object,
  handleSelectPlace: (value: object) => void,
  style?: object,
  autoFocus?: boolean,
  placeholder?: string
}

interface ISelectPlace {
  place_id: string,
  value: string,
  sessionToken: any
}

export default (props: IProps) => {
  const { map, handleSelectPlace, style, autoFocus, placeholder } = props
  const [places, setPlaces] = useState({
    options: [],
    sessionToken: ''
  })

  const autocomplete = (value: string) => {
    if (value.length) {
      autocompleteRequest({input: value}).then((response: any) => {
        setPlaces({
          options: response.options,
          sessionToken: response.sessionToken
        })
      })
    }
  }
  const autocompleteWrapped = throttle(autocomplete, 700)

  const selectPlace = (params: ISelectPlace) => {
    placesRequest({selectedPlace: params, map}).then((response: any) => {
      if (Object.keys(response).length) handleSelectPlace(response)
    })
  }

  return (
    <AutoComplete
      style={style}
      options={
        places.options.map((option: any) => {
          return {
            value: option.description,
            place_id: option.place_id
          }
        })
      }
      onSearch={value => autocompleteWrapped(value)}
      onSelect={(value, option) =>
        selectPlace({
          value: option.value,
          place_id: option.place_id,
          sessionToken: places.sessionToken
        })}
      autoFocus={autoFocus}
      placeholder={placeholder}
    >
      <Input
        type='search'
        size='large'
        style={{
          borderColor: '#5889c4',
          backgroundColor: '#fff'
        }}
        prefix={<SearchOutlined />}
      />
    </AutoComplete>
  )
}
